<template>
    <div class="header-container">
        <div class="l-content">
            <el-button @click="handleMenu" icon="el-icon-menu" size='mini' style="margin-right:20px" circle></el-button>
            <!-- 面包屑 -->
            <el-breadcrumb class="text-title" separator="/">
                <el-breadcrumb-item v-for="item in tags" :key="item.path" :to="{ path: item.path }">{{item.label}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="r-content">
            <el-dropdown>
                <span class="el-dropdown-link">
                    <img src="../assets/user-default.png" alt="默认用户" class="user" width="40px">
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item>个人中心</el-dropdown-item>
                    <el-dropdown-item>退出</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
    name:'CommonHeader',
    methods:{
        handleMenu(){
            this.$store.commit('tab/collapseMenu')
        }
    },
    computed:{
        ...mapState({
            tags:state => state.tab.tabList
        })
    },
    mounted(){
        console.log(this.tags);
    }
}
</script>

<style lang='less' scoped>
.header-container{
    height: 60px;
    background-color: #333;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    .l-content{
        display: flex;
        align-items: center;
        .text-title{
            color: #fff;
            font-size: 14px;
            margin-left: 10px;
            /deep/.el-breadcrumb__item{
                .el-breadcrumb__inner{
                    cursor: pointer;
                    font-weight: normal;
                    &.is-link{
                        color: #666;
                    }
                    
                }
                &:last-child{
                    .el-breadcrumb__inner{
                        color: #fff;
                    }
                }
            }
        }
        
    }
    .r-content{
        .user{
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }
    }
}
</style>