import Mock from 'mockjs'

// get请求从config.url获取参数，post从config.body中获取参数
function param2Obj (url) {
  const search = url.split('?')[1]
  if (!search) {
    return {}
  }
  return JSON.parse(
    '{"' +
    decodeURIComponent(search)
      .replace(/"/g, '\\"')
      .replace(/&/g, '","')
      .replace(/=/g, '":"') +
    '"}'
  )
}
function formateDate(datetime) {
  function addDateZero(num) {
      return (num < 10 ? "0" + num : num);
  }
  let d = new Date(datetime);
  let formatdatetime = d.getFullYear() + '-' + addDateZero(d.getMonth() + 1) + '-' + addDateZero(d.getDate()) + ' ' + addDateZero(d.getHours()) + ':' + addDateZero(d.getMinutes()) + ':' + addDateZero(d.getSeconds());
  return formatdatetime;
}

let List = []
const count = 100

for (let i = 0; i < count; i++) {
  List.push(
    Mock.mock({
      id: Mock.Random.guid(),
      productName: Mock.Random.ctitle(),
      operator: Mock.Random.cname(),
      'stock|100-1000': 1,
      date: Mock.Random.datetime(),
      status: Mock.Random.integer(0, 1)
    })
  )
}

export default {
  /**
   * 获取列表
   * 要带参数 name, page, limt; name可以不填, page,limit有默认值。
   * @param productName, page, limit
   * @return {{code: number, count: number, data: *[]}}
   */
  getProductList: config => {
    const { name, page = 1, limit = 20 } = param2Obj(config.url)
    // console.log('name:' + name, 'page:' + page, '分页大小limit:' + limit)
    const mockList = List.filter(product => {
      if (name && product.productName.indexOf(name) === -1 && product.operator.indexOf(name) === -1) return false
      return true
    })
    const pageList = mockList.filter((item, index) => index < limit * page && index >= limit * (page - 1))
    return {
      code: 20000,
      count: mockList.length,
      list: pageList
    }
  },
  /**
   * 增加产品
   * @param productName, addr, age, birth, sex
   * @return {{code: number, data: {message: string}}}
   */
   addProduct: config => {
    const {  productName, operator, stock, date, status } = JSON.parse(config.body)
    console.log(JSON.parse(config.body))
    List.unshift({
      id: Mock.Random.guid(),
      productName: productName,
      operator: operator,
      stock: stock,
      date: date,
      status: status
    })
    return {
      code: 20000,
      data: {
        message: '添加成功'
      }
    }
  },
  /**
   * 删除产品
   * @param id
   * @return {*}
   */
   delProduct: config => {
    const { id } = JSON.parse(config.body)
    if (!id) {
      return {
        code: -999,
        message: '参数不正确'
      }
    } else {
      List = List.filter(u => u.id !== id)
      return {
        code: 20000,
        message: '删除成功'
      }
    }
  },
  /**
   * 批量删除
   * @param config
   * @return {{code: number, data: {message: string}}}
   */
  batchremove: config => {
    let { ids } = param2Obj(config.url)
    ids = ids.split(',')
    List = List.filter(u => !ids.includes(u.id))
    return {
      code: 20000,
      data: {
        message: '批量删除成功'
      }
    }
  },
  /**
   * 修改产品
   * @param id, name, addr, age, birth, sex
   * @return {{code: number, data: {message: string}}}
   */
   editProduct: config => {
     console.log('config',config);
    const { id, productName, operator, stock, date, status } = JSON.parse(config.body)
    const status_value = parseInt(status)
    //转换时间格式
    const formate_date = formateDate(date)
console.log(formate_date);
    List.some(u => {
      if (u.id === id) {
        u.productName = productName
        u.operator = operator
        u.stock = stock
        u.date = formate_date
        u.status = status_value
        return true
      }
    })
    return {
      code: 20000,
      data: {
        message: '编辑成功'
      }
    }
  },
  /**
   * 上下架产品
   * @param id, name, addr, age, birth, sex
   * @return {{code: number, data: {message: string}}}
   */
   handleProduct: config => {
     console.log(config);
    const { id, productName, operator, stock, date, status } = JSON.parse(config)
    const status_value = parseInt(status)
    console.log(date);
    List.some(u => {
      if (u.id === id) {
        u.productName = productName
        u.operator = operator
        u.stock = stock
        u.date = date
        u.status = status_value
        return true
      }
    })
    return {
      code: 20000,
      data: {
        message: '编辑成功'
      }
    }
  }
}