<template>
  <div class="manage">
    <div class="manage-header">
      <!-- 新增按钮 -->
      <el-button type="primary" @click="handlecreate">+ 新增</el-button>

      <!-- 对话框:点击新增或编辑才会弹出表单 -->
      <!-- :before-close="closeDialog" 点击关闭的x之前要做的事情 -->
      <el-dialog :title="modalType == 0 ? '新建' : '编辑'" :visible.sync="dialogVisible" width="30%"
        :before-close="closeDialog">
        <!-- 表单Form -->
        <!-- ref=form:为了通过this.$refs调用组件的方法 -->
        <el-form  :model="form" :rules="rules" ref="form" label-width="80px">
          <!-- 每一项表单域:el-form-item -->
          <el-form-item label="产品名称" prop="productName">
            <el-input placeholder="请输入产品名称" v-model="form.productName"></el-input>
          </el-form-item>

          <el-form-item label="库存" prop="stock">
            <el-input placeholder="请输入库存数量" v-model="form.stock"></el-input>
          </el-form-item>

          <el-form-item label="状态" prop="status">
            <el-select v-model="form.status" placeholder="请输入产品状态">
              <el-option label="上架" :value="1" class="product_status"></el-option>
              <el-option label="未上架" :value="0"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="上架时间">
            <el-form-item prop="date">
              <el-date-picker
                v-model="form.date"
                type="datetime"
                placeholder="选择日期时间"
                default-time="12:00:00">
              </el-date-picker>
            </el-form-item>
          </el-form-item>
          
          <el-form-item label="操作员" prop="operator">
            <el-input placeholder="请输入操作员" v-model="form.operator"></el-input>
          </el-form-item>

          
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeDialog">取 消</el-button>
          <el-button type="primary" @click="submit">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 搜索框 -->
      <el-form :inline="true">
        <el-form-item>
          <el-input v-model="searchForm.name" placeholder="请输入要查询的产品名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="common-table">
      <!-- 用户数据Table -->
      <el-table :data="tableData" stripe style="width: 100%" height="90%">
        <el-table-column prop="productName" label="产品名称">
        </el-table-column>
        <el-table-column prop="stock" label="库存">
        </el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <span :class="scope.row.status == 1 ? 'blue':'grey'">{{ scope.row.status == 1 ? '已上架' : '未上架' }}</span>
            
          </template>
        </el-table-column>
        <el-table-column prop="date" label="时间" :fromatter="formatData">
        </el-table-column>
        <el-table-column prop="operator" label="操作员">
        </el-table-column>
        <!-- 自定义列 -->
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
            <el-button size="mini" type="primary" plain v-show="scope.row.status == '0'" @click="downFn(scope.row)" 
              >上架
            </el-button>
              <el-button size="mini" type="primary" plain v-show="scope.row.status == '1'" @click="downFn(scope.row)" 
              >下架
            </el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="pager">
        <el-pagination layout="prev, pager, next" :total="total" @current-change="currentChange">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
  
<script>
import {addProduct, getProduct,editProduct,delProduct } from '../api/index'
export default {
  data() {
    return {
      // 表单绑定的数据
      form: {
        productName: '',
        stock: '',
        date: '',
        status:'',
        operator: ''
      },
      pickerOptions: {
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
      // 表单验证规则
      rules: {
        number: [{ required: true, message: '请输入编号', trigger: 'blur' }],
        productName: [{ required: true, message: '请输入产品名称', trigger: 'blur' }],
        stock: [{ required: true, message: '请输入库存', trigger: 'blur' }],
        date: [{ required: true, message: '请输入日期', trigger: 'blur' }],
        status: [{ required: true, message: '请选择产品状态', trigger: 'blur' }],
        operator: [{ required: true, message: '请输入操作员名称', trigger: 'blur' }],
      },
      // 表单是否打开
      dialogVisible: false,
      // 列表数据
      tableData: [],
      // 打开表单:新建0,编辑1
      modalType: 0,
      // 分页的对象
      pageData: {
        page: 1,
        limit: 15
      },
      // 分页页数
      total: 0,
      // 搜索框表单
      searchForm: {
        name: ''
      }
    }
  },
  methods: {
    formatData(row, column, cellValue, index){
		
		//把传过来的日期进行回炉重造一下，又创建了一个js的 Date对象，进行重新构造，转为String字符串
		//最终返回 s 就可以了
		var s =	new Date(cellValue).toLocaleString();
	    return s;

		  // 测试参数含义：不知道参数是什么含义的就打印出来
		  // console.log(row)     	//拿到一行的所有信息，要拿到具体信息,只需要row.XXX 就可以了
		  // console.log(column)  	//拿到列的信息
		  // console.log(cellValue) //拿到单元格数据，拿到时间 2022-03-18T01:46:08.000+00:00
		  // console.log(index)     //拿到索引
	    },

    // 获取列表数据
    getProductList() {
      // 由接口文档知传入一个对象:要返回的是当前页面数据和搜索到的数据的交集
      getProduct({ params: { ...this.pageData, ...this.searchForm } }).then((data) => {
        
        console.log('data', data.data.list);
         this.tableData = data.data.list
         this.total = data.data.count || 0
      })
    },
    // 表单提交
    submit() {
      // 要用箭头函数,若用function会报错,不知道为什么
      this.$refs.form.validate((valid) => {
        // 符合校验
        if (valid) {
          // 提交数据
          if (this.modalType === 0) {
            // 新增
            addProduct(this.form).then(() => {
              this.getProductList()
            })
          } else {
            // 编辑
            editProduct(this.form).then(() => {
              this.getProductList()
            })
          }
          // 清空,关闭
          this.closeDialog()
        }
      })
    },
    // 关闭对话框
    closeDialog() {
      // 先重置
      this.$refs.form.resetFields()
      // 后关闭
      this.dialogVisible = false
    },
    // 编辑按钮
    handleEdit(index) {
      this.modalType = 1
      this.openForm()
      // 深拷贝
      this.form = JSON.parse(JSON.stringify(index))
    },
    // 删除按钮
    handleDelete(index) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        console.log('index',index);
        // 删除操作:根据后端接口,参数是对象,id是唯一标识符
        delProduct({ id: index.id }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.getProductList()
        });
      }).catch(() => {
        // 点击取消:不删除了
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    // 上下架功能
    downFn(row){
      if(row.status =='1'){
          this.$confirm('是否下架该产品?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log('row',row);
          // 删除操作:根据后端接口,参数是对象,id是唯一标识符
          row.status = '0'
          console.log('row--',row);
          editProduct(row).then(() => {
            this.$message({
              type: 'success',
              message: '下架成功!'
            })
            this.getProductList()
          });
        }).catch(() => {
          // 点击取消:不删除了
          this.$message({
            type: 'info',
            message: '已取消下架'
          });
        });
      }else{
        this.$confirm('是否上架该产品?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log('row',row);
          // 删除操作:根据后端接口,参数是对象,id是唯一标识符
          row.status = '1'
          console.log('row--',row);
          editProduct(row).then(() => {
            this.$message({
              type: 'success',
              message: '上架成功!'
            })
            this.getProductList()
          });
        }).catch(() => {
          // 点击取消:不删除了
          this.$message({
            type: 'info',
            message: '已取消上架'
          });
        });
      }
    },
    // 新建按钮
    handlecreate() {
      this.modalType = 0
      this.openForm()
    },
    // 打开表单
    openForm() {
      this.dialogVisible = true
    },
    // 改变页码
    currentChange(val) {
      this.pageData.page = val
      this.getProductList()
    },
    // 搜索
    search() {
      this.getProductList()
    }
    
  },
  
  mounted() {
    this.getProductList()
  }
}
</script>
  
<style lang="less" scoped>
.manage {
  height: 100%;

  .manage-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .common-table {
    height: 90%;
    position: relative;
    .blue{
      color: rgb(0, 132, 255);
    }
    .pager {
      position: absolute;
      right:20px;
      bottom: 0;
    }
  }
}
</style>