import Mock from 'mockjs'
import homeApi from '../api/mockServerData/home'
import user from './user'
import mall from './mockServerData/product'
//定义请求拦截
Mock.mock('/api/home/getData',homeApi.getStatisticalData)

// 用户管理:增删查改
Mock.mock(/\/api\/user\/get/,user.getUserList)
Mock.mock('/api/user/create','post',user.createUser)
Mock.mock('/api/user/update','post',user.updateUser)
Mock.mock('/api/user/del','post',user.deleteUser)


// 商品管理:增删查改
Mock.mock(/\/api\/mall\/get/,mall.getProductList)
Mock.mock('/api/mall/create','post',mall.addProduct)
Mock.mock('/api/mall/update','post',mall.editProduct)
Mock.mock('/api/mall/del','post',mall.delProduct)