<template>
  <div>
    <el-row>
      <el-col :span="8">
          <el-card class="box-card">
            <div class="user">
              <img src="../assets/user-default.png" alt="用户头像">
              <div class="userinfo">
                <p class="name">username</p>
                <p class="access">超级管理员</p>
              </div>
            </div>
              <div class="text item">
                <p>上次登录时间<span>2022-7-19</span></p>
                <p>上次登录地点<span>长沙</span></p>
              </div>
          </el-card>
          <el-card class="box-card table-box">
            <el-table
              :data="tableData"
              style="width:100%">
              <el-table-column
              v-for="(val,key) in tableLabel" :key="key"
                :prop="key"
                :label="val" />
            </el-table>
          </el-card>
      </el-col>
      <el-col :span="16">
        <div class="orderDetails" >
          <el-card class="orderCard"
          v-for="card in countData" :key="card.name"
          :body-style="{ display:'flex', padding: 0}">
            <i class="icon" :class="`el-icon-${card.icon}`"
              :style="{ background:card.color}">
            </i>
            <div class="details">
              <p class="price">￥{{card.value}}</p>
              <p class="detail">{{card.name}}</p>
            </div>
          </el-card>
        </div>
        <div>
          <!-- 折线图 -->
          <el-card class="line-graph" style='height:280px'>
            <div ref="echarts1" style="height:280px"></div>
          </el-card>
        </div>
        <div class="graph-groups">
          <!-- 柱状图 -->
          <el-card class="histogram" id="echarts2" ></el-card>
          <el-card class="pie-chart" id="echarts3" ></el-card>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { getData } from "../api"
import * as echarts from "echarts"   //echarts5.0版本引入格式
    export default {
        name:'Home',
        data() {
          return {
            tableData: [
            {
              name: 'oppo',
              todayBuy: 100,
              monthBuy: 300,
              totalBuy: 800
            },
            {
              name: 'vivo',
              todayBuy: 100,
              monthBuy: 300,
              totalBuy: 800
            },
            {
              name: '苹果',
              todayBuy: 100,
              monthBuy: 300,
              totalBuy: 800
            },
            {
              name: '小米',
              todayBuy: 100,
              monthBuy: 300,
              totalBuy: 800
            },
            {
              name: '三星',
              todayBuy: 100,
              monthBuy: 300,
              totalBuy: 800
            },
            {
              name: '魅族',
            todayBuy: 100,
              monthBuy: 300,
              totalBuy: 800
            }
          ],
          tableLabel:{
            name:'商品',
            todayBuy:'今日购买',
            monthBuy:'本月购买',
            totalBuy:'总购买'
          },
          countData: [
            {
              name: "今日支付订单",
              value: 1234,
              icon: "success",
              color: "#2ec7c9",
            },
            {
              name: "今日收藏订单",
              value: 210,
              icon: "star-on",
              color: "#ffb980",
            },
            {
              name: "今日未支付订单",
              value: 1234,
              icon: "s-goods",
              color: "#5ab1ef",
            },
            {
              name: "本月支付订单",
              value: 1234,
              icon: "success",
              color: "#2ec7c9",
            },
            {
              name: "本月收藏订单",
              value: 210,
              icon: "star-on",
              color: "#ffb980",
            },
            {
              name: "本月未支付订单",
              value: 1234,
              icon: "s-goods",
              color: "#5ab1ef",
            },
          ],
        }
      },
      mounted(){
        getData().then(({data}) => {
          const {tableData} = data.data
          console.log(data.data);
          this.tableData = tableData
          
          //折线图
          const echarts1 = echarts.init(this.$refs.echarts1)
           var echarts1Option = {}
          //处理数据xAxis
          const { orderData,userData,videoData} = data.data
          const xAxis = Object.keys(orderData.data[0])
          const xAxisData = {
            data:xAxis
          }
          echarts1Option.xAxis = xAxisData
          echarts1Option.yAxis ={}
          echarts1Option.legend = xAxisData
          echarts1Option.series = []
          xAxis.forEach(key => {
            echarts1Option.series.push({
              name: key,
              data: orderData.data.map(item => item[key]),
              type:'line'
            })
          })
          echarts1.setOption(echarts1Option)

          //柱状图
          const echarts2 = echarts.init(document.getElementById('echarts2'))
          const xAxis2Data = []
          const activeData = []
          const newData = []
          userData.forEach(key =>{
            xAxis2Data.push(key.date)
            activeData.push(key.active)
            newData.push(key.new)
          })
          console.log(xAxis2Data,activeData,newData);
          var echarts2Option = {
              xAxis: {
                data:xAxis2Data
              },
              yAxis: {},
              series: [
                {
                  type:'bar',
                  data:activeData
                },
                {
                  type:'bar',
                  data:newData
                }
              ]
          }
          echarts2.setOption(echarts2Option)

          const echarts3 = echarts.init(document.getElementById('echarts3'))
          var video = {
          tooltip: {
            trigger: "item",
          },
          color: [
            "#0f78f4",
            "#dd536b",
            "#9462e5",
            "#a6a6a6",
            "#e1bb22",
            "#39c362",
            "#3ed1cf",
          ],
          series: [
            {
              data:videoData,
              type:'pie'
            }
          ],
        }
          echarts3.setOption(video)
        })
        //基于准备好的dom，初始化echarts实例
        
      }
    }
</script>

<style lang='less' scoped>
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both;
  }

  .box-card {
    width: 500px;
    img{
        height: 150px;
        widows: 150px;
        margin-right: 40px;
        border-radius: 50%;
      }
    .user{
      display: flex;
      align-items: center;
      padding-bottom: 20px;
      border-bottom: #ddd 1px solid;
      margin-bottom: 20px;
      .userinfo{
      .name{
        font-size: 32px;
        margin-bottom: 10px;
      }
    }
    }
    .text {
      font-size: 14px;
      p{
        font-size: 14px;
        color: #999;
        width: 400px;
        line-height: 28px;
        span{
          float: right;
        }
      }
    }
  }
  .table-box{
    height: 460px;
    margin-top: 20px;
  }
  .orderDetails{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    .orderCard{
      width: 32%;
      margin-bottom: 20px;
      .icon{
        width: 80px;
        height: 80px;
        font-size: 30px;
        text-align: center;
        line-height: 80px;
        color: #fff;
      }
      .details{
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-left: 15px;
        .price{
          font-size: 30px;
          margin-bottom: 10px;
          line-height: 30px;
          height: 30px;
        }
        .detail{
          font-size: 14px;
          color: #999;
          text-align: center;
        }
      }
    }
  }
  .line-graph{
    height: 280px;
    margin-bottom: 20px;
  }
  .graph-groups{
    display: flex;
    justify-content: space-between; //贴边
    .pie-chart,.histogram{
      height: 260px;
      width: 48%;
    }
    
  }
</style>