export default{
    namespaced:true,
    actions:{
    },
    mutations:{
        //修改菜单展开和收集 
        collapseMenu(state){
            state.isCollapse = !state.isCollapse
        },
        //更新面包屑shuju 
        selectMenu(state,value){
            console.log(value)
            //判断添加的数据是否为首页
            if(value.name !== 'home'){
                const index = state.tabList.findIndex(item => item.name === value.name)
                //如果不存在
                if(index === -1){
                    state.tabList.push(value)
                }
            }
        },
        //删除指定的tag数据
        closeTag(state,item){
            console.log(item)
            const index = state.tabList.findIndex(val => val.name === item.name)
            state.tabList.splice(index,1)
        }
    },
    state:{
        isCollapse: false, //控制菜单展开还是收起
        tabList:[
            {
                path: "/",
                name: "home",
                label: "首页",
                icon: "s-home",
                url: "Home/Home",
              },
        ]

    },
    getters:{

    }
}